import React from 'react';
import "./style.css";

const ScoreComponent =  ({playerXCount,playerOCount}) => {
    return (
        <div className="score">
            <div className="container">
                <div className="score__players">
                    <div className="player__one">
                        <h3>Player X: <i>{playerXCount}</i></h3>
                    </div>
                    <div className="player__two">
                        <h3>Player O: <i>{playerOCount}</i></h3>
                    </div>
                </div>
            </div>
        </div>
    )
};
export default ScoreComponent;