import React from 'react';
import './App.css';
import {Route} from "react-router-dom";
//Modules
import {Score} from "./modules/score_module";
import {Menu} from "./modules/main_menu_module";
import {Game} from "./modules";
import {PveGame} from "./modules";

function App() {
  return (
    <div className="wrap">
        <Route exact path="/" component={Menu} />
        <Route path="/play" component={Score} />
        <Route path="/pvp" component={Game} />
        <Route path="/pve" component={PveGame}/>
    </div>
  );
}

export default App;
