    componentDidUpdate(prevProps, prevState, snapshot) {
        if (this.state.botStep !== prevState.botStep) {
            this.aiStep(this.state.botStep);
        }
    }

    onHandleClick(event) {
        const {step} = this.state;
        if (event.currentTarget.id === "on") {
            document.getElementsByClassName(event.currentTarget.className)[0].id = "off";
            if (step === 0) {
                event.target.innerHTML = "<i class='fas fa-times'> </i>";
                event.target.id = "cross";
                this.setState({step: 1})
            }
            else {
                event.target.innerHTML = "<i class=\"far fa-circle\"> </i>";
                event.target.id = "zero";
                this.setState({step: 0})
            }
            //document.getElementsByClassName(event.target.className)[0].appendChild(li)
            this.setState({item: event.target.id})
        }
        this.setState({
            botStep: this.state.botStep + 1
        });
        this.winnerChecker();
    }

    render() {
        const {item,step,message,zeroPlayer,crossPlayer} = this.state;
        return (
            <div>
                <PveGameComponent
                    item={item}
                    message={message}
                    onHandleClick={this.onHandleClick}
                    reloadClick={this.reloadClick}
                    reloadScore={this.reloadScore}
                    step={step}
                    zeroPlayer={zeroPlayer}
                    crossPlayer={crossPlayer}
                />
            </div>
        );
    }
