import React from 'react';
import "./style.css";

import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faRedoAlt,faSquare,faArrowAltCircleLeft} from "@fortawesome/free-solid-svg-icons";
import {Link} from "react-router-dom";

const GameComponent = ({reloadScore,onHandleClick,message,reloadClick,step,zeroPlayer,crossPlayer}) => {
    return (
        <div className="game">
            <h4>{(step === 0) ? crossPlayer : zeroPlayer}</h4>
            <div className="container grid_square">
                <div className="game_square square_0" id="on" onClick={onHandleClick} >
                    <li className="null">

                    </li>
                </div>
                <div className="game_square square_1" id="on" onClick={onHandleClick}>
                    <li className="null">

                    </li>
                </div>
                <div className="game_square square_2" id="on" onClick={onHandleClick}>
                    <li className="null">

                    </li>
                </div>
                <div className="game_square square_3" id="on" onClick={onHandleClick}>
                    <li className="null">

                    </li>
                </div>
                <div className="game_square square_4" id="on" onClick={onHandleClick}>
                    <li className="null">

                    </li>
                </div>
                <div className="game_square square_5" id="on" onClick={onHandleClick}>
                    <li className="null">

                    </li>
                </div>
                <div className="game_square square_6" id="on" onClick={onHandleClick}>
                    <li className="null">

                    </li>
                </div>
                <div className="game_square square_7" id="on" onClick={onHandleClick}>
                    <li className="null">

                    </li>
                </div>
                <div className="game_square square_8" id="on" onClick={onHandleClick}>
                    <li className="null">

                    </li>
                </div>
            </div>
            <div className="container">
                <button className="toMenu"><Link to="/"><FontAwesomeIcon icon={faArrowAltCircleLeft} /></Link></button>
                <button className="reloadBtn" onClick={reloadClick}><FontAwesomeIcon icon={faRedoAlt} /></button>
                <button className="reloadScore" onClick={reloadScore}><FontAwesomeIcon icon={faSquare} /></button>
            </div>
        </div>
    )
};
export default GameComponent;